@import "../../global.scss";

.navbar {
    width: 100%;
    height: 70px;
    background-color: white;
    color: $main-color;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            display: flex;
            align-items: center;

            .logo{
                font-size: 40px;
                text-decoration: none;
                font-weight: 700;
                color: inherit;
            }

            .item-container{
                display: flex;
                align-items: center;
                margin-left: 30px;
                padding: 5px;
                border-radius: 5px;

                .icon{
                    font-size: 25px;
                    margin-right: 5px;
                }

                span{
                    font-size: 15px;
                    font-weight: 500;
                }

                a {
                    text-decoration: none;
                    color: inherit;
                }

                &:hover {
                    background-color: rgb(220, 220, 220);
                }
            }

        }
        .right{

            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;

                span{
                    width: 100%;
                    height: 3px;
                    background-color: $main-color;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }

    // Change navbar color and rotate hamburger to an x
    &.active{
        background-color: $main-color;
        color: white;

        .hamburger{
            
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}